@import './../../scss/variables';

.contact {
  background-color: $primary-color;
  min-height: 40vmin;
  padding-top: 5rem;

  .contact__heading {
    text-align: center;
    font-size: 10vmin;
  }
}

.contact__logos {
  display: flex;
  justify-content: center;


  .contact__logo-item {
    transition: ease-out 300ms;
    position: relative;

    .contact__tooltip {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: nowrap;
      background-color: rgba($color: #000000, $alpha: 0.5);
      width: 8rem;
      height: fit-content;
      box-sizing: border-box;
      padding: 0.5rem;
      border-radius: 5px;
      color: $white-color;
      position: absolute;
      z-index: 1;
      top: -0.2rem;
      left: -0.9rem;
    }

    img {
      // width: 60px;
      margin: 3rem 1.5rem;
      transition: ease-in 300ms;
      cursor: pointer;

      &:hover {
        filter: invert(100%) sepia(17%) saturate(3387%) hue-rotate(176deg) brightness(102%) contrast(98%);
        transition: ease-in 300ms;
      }
    }
  }
}